'use client';

import React, { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { useAppContext } from '../context/AppContext';
import logo from '../assets/logo.svg';
import Footer from './Footer';

// Styled components
import {
  PortfolioContainer,
  PortfolioHeader,
  PortfolioHero,
  PortfolioFilter,
  PortfolioGrid,
  ProjectCard,
  ProjectImage,
  ProjectOverlay,
  ProjectInfo,
  ProjectCategory,
  ProjectTitle,
  ProjectDescription,
  ProjectButton,
  PortfolioCTA,
  LoadingSpinner,
  FilterButton
} from '../styles/NewPortfolioStyles';

const NewPortfolioPage = () => {
  const router = useRouter();

const navigate = (path: string) => {
  router.push(path);
};
  const { user, cartItems } = useAppContext();
  const [activeFilter, setActiveFilter] = useState('All');
  const [projects, setProjects] = useState<Array<{
    id: number;
    title: string;
    category: string;
    description: string;
    imageClass: string;
  }>>([]);
  const [loading, setLoading] = useState(true);

  const cartCount = cartItems.reduce((total, item) => total + item.quantity, 0);

  // Sample portfolio projects data
  useEffect(() => {
    // Simulate loading projects
    const sampleProjects = [
      {
        id: 1,
        title: "Modern Minimalist Living Room",
        category: "Residential",
        description: "Clean lines and contemporary aesthetics with a focus on functionality and natural light.",
        imageClass: "modern"
      },
      {
        id: 2,
        title: "Classic Elegance Dining Space",
        category: "Residential",
        description: "Timeless designs with refined details featuring rich textures and elegant furnishings.",
        imageClass: "classic"
      },
      {
        id: 3,
        title: "Coastal Retreat Bedroom",
        category: "Residential",
        description: "Light, airy spaces with natural elements and soothing color palettes inspired by the ocean.",
        imageClass: "coastal"
      },
      {
        id: 4,
        title: "Corporate Office Design",
        category: "Commercial",
        description: "Professional workspace that balances productivity with comfort and aesthetic appeal.",
        imageClass: "office"
      },
      {
        id: 5,
        title: "Boutique Hotel Lobby",
        category: "Hospitality",
        description: "Luxurious and inviting space that creates a memorable first impression for guests.",
        imageClass: "hotel"
      },
      {
        id: 6,
        title: "Restaurant Interior",
        category: "Commercial",
        description: "Atmospheric dining environment that enhances the culinary experience.",
        imageClass: "restaurant"
      }
    ];

    setProjects(sampleProjects);
    setLoading(false);
  }, []);

  const filterButtons = ['All', 'Residential', 'Commercial', 'Hospitality'];

  const filteredProjects = activeFilter === 'All'
    ? projects
    : projects.filter(project => project.category === activeFilter);

  if (loading) {
    return (
      <PortfolioContainer>
        <LoadingSpinner />
      </PortfolioContainer>
    );
  }

  return (
    <PortfolioContainer>
      {/* Navigation Bar */}
      <PortfolioHeader>
        <div className="nav-container">
          <div className="nav-logo">
            <a href="#" className="logo-link" onClick={(e) => { e.preventDefault(); navigate('/'); }}>
              <div className="logo-container">
                <img 
                  src={logo} 
                  alt="Colour My Space Logo" 
                  className="logo-image" 
                />
              </div>
            </a>
          </div>
          <div className="nav-menu">
            <a href="#" className="nav-link" onClick={(e) => { e.preventDefault(); navigate('/'); }}>Home</a>
            <a href="#" className="nav-link" onClick={(e) => { e.preventDefault(); navigate('/shop'); }}>Shop</a>
            <a href="#" className="nav-link active" onClick={(e) => { e.preventDefault(); navigate('/portfolio'); }}>Portfolio</a>
            <a href="#" className="nav-link" onClick={(e) => { e.preventDefault(); navigate('/services'); }}>Services</a>
            {user && (
              <a href="#" className="nav-link" onClick={(e) => { e.preventDefault(); navigate('/orders'); }}>Orders</a>
            )}
            <a href="#" className="nav-link" onClick={(e) => { e.preventDefault(); navigate('/about'); }}>About</a>
            <a href="#" className="nav-link" onClick={(e) => { e.preventDefault(); navigate('/contact'); }}>Contact</a>
          </div>
          <div className="nav-icons">
            <button className="nav-icon" onClick={() => navigate('/search')}>
              <i className="fas fa-search"></i>
            </button>
            {user ? (
              <>
                <button className="nav-icon" onClick={() => navigate('/auth')}>
                  <i className="fas fa-sign-out-alt"></i>
                </button>
                <span className="user-greeting">Hi, {user.name}</span>
              </>
            ) : (
              <button className="nav-icon" onClick={() => navigate('/auth')}>
                <i className="fas fa-user"></i>
              </button>
            )}
            <button className="nav-icon" onClick={() => navigate('/cart')}>
              <i className="fas fa-shopping-cart"></i>
              {cartItems.length > 0 && <span className="cart-count">{cartCount}</span>}
            </button>
          </div>
        </div>
      </PortfolioHeader>

      {/* Hero Section */}
      <PortfolioHero>
        <div className="hero-content">
          <h1>Design Portfolio</h1>
          <p>Showcasing our finest interior design projects across residential, commercial, and hospitality spaces.</p>
        </div>
      </PortfolioHero>

      {/* Portfolio Filter */}
      <PortfolioFilter>
        <div className="filter-container">
          <div className="filter-buttons">
            {filterButtons.map((filter) => (
              <FilterButton
                key={filter}
                $active={activeFilter === filter}
                onClick={() => setActiveFilter(filter)}
              >
                {filter}
              </FilterButton>
            ))}
          </div>
        </div>
      </PortfolioFilter>

      {/* Portfolio Grid */}
      <PortfolioGrid>
        <div className="projects-container">
          {filteredProjects.length > 0 ? (
            <div className="projects-grid">
              {filteredProjects.map((project) => (
                <ProjectCard key={project.id}>
                  <ProjectImage imageClass={project.imageClass as any} />
                  <ProjectOverlay>
                    <ProjectInfo>
                      <ProjectCategory>{project.category}</ProjectCategory>
                      <ProjectTitle>{project.title}</ProjectTitle>
                      <ProjectDescription>{project.description}</ProjectDescription>
                      <ProjectButton>View Project</ProjectButton>
                    </ProjectInfo>
                  </ProjectOverlay>
                </ProjectCard>
              ))}
            </div>
          ) : (
            <div className="no-projects">
              <h3>No projects found for this category</h3>
            </div>
          )}
        </div>
      </PortfolioGrid>

      {/* CTA Section */}
      <PortfolioCTA>
        <div className="cta-content">
          <h2>Ready to Transform Your Space?</h2>
          <p>Let our expert designers create a space that reflects your unique style and meets your needs.</p>
          <button className="cta-button">Schedule a Consultation</button>
        </div>
      </PortfolioCTA>

      <Footer />
    </PortfolioContainer>
  );
};

export default NewPortfolioPage;